import styled from "styled-components";

export const ContactEmail = "unstabler@unstabler.pl"

export const MobileWidth = 480;
export const TabletWidth = 720;
export const DesktopMinWidth = 1280;

export const tabletQuery = `@media screen and (min-width: ${MobileWidth + 1}px) and (max-width: ${DesktopMinWidth - 1}px)`;
export const mobileQuery = `@media screen and (max-width: ${MobileWidth}px)`;
export const tabletAndMobileQuery = `@media screen and (max-width: ${DesktopMinWidth - 1}px)`;
export const pcQuery = `@media screen and (min-width: ${DesktopMinWidth}px)`;

export const section1Query = `@media screen and (min-width: ${MobileWidth + 1}px) and (max-width: 815px)`;
export const section1Query2 = `@media screen and (min-width: ${MobileWidth + 1}px) and (max-width: 818px)`;
export const section1Query3 = `@media screen and (max-width: 760px)`;
export const section2Query = `@media screen and (min-width: ${MobileWidth + 1}px) and (max-width: 805px)`;
export const lastSectionQuery = `@media screen and (min-width: ${MobileWidth + 1}px) and (max-width: 620px)`;

export const below1080Query = `@media screen and (max-width: ${1080 + 70}px)`;

export const NewLineIfDesktop = styled.div`
  display: block;

  ${tabletQuery} {
    display: none;
  }

  ${mobileQuery} {
    display: none;
  }
`
export const NewLineIfNotDesktop = styled.div`
  display: none;

  ${tabletQuery} {
    display: block;
  }

  ${mobileQuery} {
    display: block;
  }
`

export const NewLineIfMobile = styled.div`
  display: none;
  
  ${tabletQuery} {
    display: none;
  }
  
  ${mobileQuery} {
    display: block;
  }
`
export const NewLineIfNotMobile = styled.div`
  display: block;

  ${tabletQuery} {
    display: block;
  }

  ${mobileQuery} {
    display: none;
  }
`
export const VisibleIfNotMobile = styled.span`
  display: initial;

  ${tabletQuery} {
    display: initial;
  }

  ${mobileQuery} {
    display: none;
  }
`
