import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';

import x from '../assets/bg1.png'

const Canvas = styled.canvas`
    position: fixed;
    left: 0; top: 0;
  
    background: white;
    width: 100vw;
    height: 100vh;
  
    z-index: -1;
`;

class BackgroundRenderer {
    public context: CanvasRenderingContext2D;
    public image: HTMLImageElement;
    public isImageReady: boolean = false;
    public interval: any = null;

    public dx: number = 0;
    public dy: number = 0;

    constructor(public canvas: HTMLCanvasElement) {
        this.context = canvas.getContext('2d')!;
        this.image = document.createElement('img');
        this.image.src = x;
        this.image.addEventListener('load', () => {
            this.isImageReady = true;
            console.log(this.image);
        });


        window.addEventListener('resize', this.handleResize);
        this.handleResize();
    }

    readonly handleResize = () => {
        this.canvas.width = window.innerWidth * 0.75;
        this.canvas.height = window.innerHeight * 0.75;
    }

    start() {
        this.interval = setInterval(this.render.bind(this), 1000 / 24);
    }

    render() {
        if (!this.isImageReady) {
            return;
        }
        const t = (1000 / 24);

        const { width, height } = this.canvas;
        const context = this.context;
        const degrees = 45;

        const factor = 0.75;
        const imageHeight = 480 * factor;
        const imageWidth = (this.image.naturalWidth * imageHeight) / this.image.naturalHeight;

        context.clearRect(0, 0, width, height);

        context.save();
        context.rotate(degrees * Math.PI / 180);
        for (let y = -height * 2; y <= height * 4; y = y + imageHeight) {
            for (let x = -width * 2; x <= width * 4; x = x + imageWidth) {
                this.context.drawImage(this.image, x + this.dx, y + this.dy, imageWidth, imageHeight);
            }
        }
        context.restore();


        context.translate(0, 0);

        if (Math.abs(this.dx) >= imageWidth) {
            this.dx = 0;
        }

        if (Math.abs(this.dy) >= imageHeight) {
            this.dy = 0;
        } else {
            this.dy -= ((t / (1000 * 15)) * imageHeight);
        }
    }

    finalize() {
        clearInterval(this.interval);
        window.removeEventListener('resize', this.handleResize);
    }
}

const AppBackground: React.FC = () => {
    const canvasRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) {
            return;
        }

        const renderer = new BackgroundRenderer(canvas);
        renderer.start();

        return () => {
            renderer.finalize();
        };
    }, [canvasRef]);

    return (
        <Canvas ref={canvasRef} width="1280" height="720" />
    );
};

export default AppBackground;
