import React, {useCallback, useEffect, useState} from "react";

import {
    ContactEmail,
    DesktopMinWidth
} from "../global/constants";

import {
    HeaderPosition, HeaderRoot, HeaderContent, HeaderLogo, HeaderItems, ContactButton, MenuButton,
    HeaderItemRoot,
    DropdownRoot, DropdownItems
} from "../styled_containers/HeaderContainers";

import empty from "../assets/empty.png"

type HeaderProps = {
    fixed: boolean
    scrollIntoTargets: HTMLDivElement[]
}

const Header: React.FC<HeaderProps> = (props) => {

    const [dropdownOpen, setDropdownOpen] = useState(false);

    useEffect(() => {
        const resized = () => {
            if (window.innerWidth < DesktopMinWidth - 1)
                setDropdownOpen(false);
        };

        window.addEventListener("resize", resized);
        return () => window.removeEventListener("resize", resized);
    }, []);

    return (
        <HeaderPosition fixed={props.fixed || dropdownOpen}>
            <HeaderRoot>
                <HeaderContent>
                    <HeaderLogo src={empty}/>
                    <HeaderItems>
                        <HeaderItem text={"주요 업무"} bold={false}
                                    onclick={() => props.scrollIntoTargets[0].scrollIntoView({behavior: "smooth"})}/>
                        <HeaderItem text={"팀원 소개"} bold={false}
                                    onclick={() => props.scrollIntoTargets[1].scrollIntoView({behavior: "smooth"})}/>
                        <a href={`mailto:${ContactEmail}`}>
                            <ContactButton>
                                Contact Us
                            </ContactButton>
                        </a>
                        <MenuButton onClick={() => setDropdownOpen(!dropdownOpen)}>
                            <div/><div/><div/>
                        </MenuButton>
                    </HeaderItems>
                </HeaderContent>
                <HeaderDropdown sectionPositions={props.scrollIntoTargets} open={dropdownOpen} height={190}
                                closeIt={() => setDropdownOpen(false)}/>
            </HeaderRoot>
        </HeaderPosition>
    )
}

export default Header;

export type HeaderItemProps = {
    text: string
    bold: boolean
    onclick: () => void
}

const HeaderItem: React.FC<HeaderItemProps> = (props) => {
    return (
        <HeaderItemRoot onClick={props.onclick}>
            {props.bold ? <b>{props.text}</b> : props.text}
        </HeaderItemRoot>
    )
}

type HeaderDropdownProps = {
    open: boolean
    height: number
    sectionPositions: HTMLDivElement[]
    closeIt: () => void
}

const HeaderDropdown: React.FC<HeaderDropdownProps> = (props) => {

    const selectAndClose = useCallback((index: number) => {
        props.sectionPositions[index].scrollIntoView({behavior: "smooth"})
        props.closeIt()
    }, [props]);

    return (
        <DropdownRoot open={props.open} height={props.height}>
            <DropdownItems>
                <li onClick={() => selectAndClose(0)}>회사 소개</li>
                <li onClick={() => selectAndClose(1)}>주요 업무</li>
                <li onClick={() => selectAndClose(2)}>팀원 소개</li>
            </DropdownItems>
        </DropdownRoot>
    );
}
