import React, {useCallback, useEffect, useMemo, useRef, useState} from "react";
import {throttle} from "lodash";

import {
    SectionRoot, SectionHeader, Subtitle, Title, TeammatesRoot, SafariShitPadding, ScrollbarRoot, ScrollbarThumb,
    TeammateRoot, TeammateCard, TeammateProfilePic, TeammateName, TeammatePosition, TeammateComment, TeammateSnsRoot, TeammateSns,
    EmptyTeammateRoot, EmptyTeammateCard, EmptyTeammateImageWrapper,
    Linkedin, Email, Blog, Github, Mastodon, RocketPunch, Twitter, Facebook
} from "../../../../styled_containers/areas/main/sections/teammates/TeammatesContainers";

import icon_linkedin_on from "../../../../assets/sns_icons/linkedin_on.png";
import icon_linkedin_off from "../../../../assets/sns_icons/linkedin_off.png";
import icon_twitter_on from "../../../../assets/sns_icons/twitter_on.png";
import icon_twitter_off from "../../../../assets/sns_icons/twitter_off.png";
import icon_github_on from "../../../../assets/sns_icons/github_on.png";
import icon_github_off from "../../../../assets/sns_icons/github_off.png";
import icon_facebook_on from "../../../../assets/sns_icons/facebook_on.png";
import icon_facebook_off from "../../../../assets/sns_icons/facebook_off.png";
import icon_email_on from "../../../../assets/sns_icons/email_on.png";
import icon_email_off from "../../../../assets/sns_icons/email_off.png";
import icon_blog_on from "../../../../assets/sns_icons/blog_on.png";
import icon_blog_off from "../../../../assets/sns_icons/blog_off.png";
import icon_mastodon_on from "../../../../assets/sns_icons/mastodon_on.png";
import icon_mastodon_off from "../../../../assets/sns_icons/mastodon_off.png";
import icon_rocketpunch_on from "../../../../assets/sns_icons/rocketpunch_on.png";
import icon_rocketpunch_off from "../../../../assets/sns_icons/rocketpunch_off.png";

import logo_enabled from "../../../../assets/logo_enabled.png";
import logo_disabled from "../../../../assets/logo_disabled.png";

import teammates from "../../../../data/teammates.json";
import {ScrollInto} from "../../../../App";

const customSort = ["Chief Executive Officer", "Chief Operating Officer", "Management Support", "Developer", "Designer"];
const developerSort = ["Frontend", "Backend", ""];

teammates.sort((a, b) => {
    if (a.position !== b.position)
        return customSort.indexOf(a.position) - customSort.indexOf(b.position);
    else {
        if (a.position === "Developer") {
            return developerSort.indexOf(a.department || "") - developerSort.indexOf(b.department || "");
        } else {
            return 0;
        }
    }
});

const maxRows = Math.ceil(teammates.length / 3);

const Teammates: React.FC<{scrollInto: ScrollInto}> = (props) => {

    const rootElement = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (rootElement.current == null) return
        props.scrollInto.out.current[props.scrollInto.index] = rootElement.current;
    }, [props.scrollInto.index, props.scrollInto.out]);

    const scrollableElement = useRef<HTMLDivElement>(null);
    const scrollbarElement = useRef<HTMLDivElement>(null);
    const scrollbarThumbElement = useRef<HTMLDivElement>(null);
    const scrollbarTimeout = useRef<NodeJS.Timeout | null>(null);

    const [profilePictures, setProfilePictures] = useState<any[]>([]);

    const [scrollbarWidth, setScrollbarWidth] = useState(0);
    const [scrollbarThumbWidth, setScrollbarThumbWidth] = useState(0);
    const [scrolling, setScrolling] = useState(false);

    useEffect(() => {
        Promise.all(teammates.map((teammate) => import(`../../../../assets/profile_pics/profile_pic_${teammate.profile_pic}.png`)))
            .then((results) => setProfilePictures(results));
    }, []);

    const throttledScroll = useMemo(
        () =>
            throttle(() => {
                if (scrollbarTimeout.current)
                    clearTimeout(scrollbarTimeout.current);

                setScrolling(true);

                scrollbarTimeout.current = setTimeout(() => {
                    setScrolling(false);
                }, 750);
            }, 10),
        []);

    const updateScrollbarThumb = useCallback(() => {
        if (!scrollbarThumbElement.current || !scrollableElement.current) return;

        scrollbarThumbElement.current.style.transform = `translateX(${(scrollableElement.current.scrollLeft) / (scrollableElement.current.scrollWidth - scrollableElement.current.clientWidth) * scrollbarWidth}px)`
    }, [scrollbarWidth]);

    const updateScrollbarThumbWidth = useCallback(() => {
        if (!scrollableElement.current) return;

        const newScrollThumbWidth = window.innerWidth / scrollableElement.current.scrollWidth * window.innerWidth;
        setScrollbarThumbWidth(newScrollThumbWidth);
    }, [scrollableElement]);

    useEffect(() => {
        updateScrollbarThumbWidth();
    }, [scrollableElement, updateScrollbarThumbWidth]);

    useEffect(() => {
        if (!scrollbarElement.current) return;

        setScrollbarWidth(scrollbarElement.current.clientWidth - scrollbarThumbWidth);
    }, [scrollbarElement, scrollbarThumbWidth]);

    useEffect(() => {
        const element = scrollableElement.current;

        element?.addEventListener("scroll", throttledScroll);
        element?.addEventListener("scroll", updateScrollbarThumb);
        window.addEventListener("resize", updateScrollbarThumbWidth);
        return () => {
            element?.removeEventListener("scroll", throttledScroll);
            element?.removeEventListener("scroll", updateScrollbarThumb);
            window.removeEventListener("resize", updateScrollbarThumbWidth);
        }
    }, [throttledScroll, updateScrollbarThumb, updateScrollbarThumbWidth]);


    return (
        <SectionRoot ref={rootElement}>
            <SectionHeader>
                <Subtitle><span className={"yellow"}>팀원 소개</span></Subtitle>
                <Title>함께하는 팀원을<br/>소개합니다</Title>
            </SectionHeader>
            <TeammatesRoot ref={scrollableElement} isElementScrolling={scrolling}>
                {(new Array(maxRows * 3)).fill(1).map((_, index) => {
                    if (index < teammates.length) {
                        const teammate = teammates[index];
                        return (
                            <Teammate
                                key={`teammate_${index}`}
                                profilePic={profilePictures[index] || ""}
                                name={teammate.name}
                                position={teammate.position}
                                sns={teammate.sns}>
                                {teammate.comment}
                            </Teammate>
                        )
                    } else {
                        return (
                            <EmptyTeammate key={`teammate_${index}`}/>
                        )
                    }
                })}
                <SafariShitPadding>&nbsp;</SafariShitPadding>
            </TeammatesRoot>
            <ScrollbarRoot ref={scrollbarElement} visible={scrolling}>
                <ScrollbarThumb ref={scrollbarThumbElement} size={scrollbarThumbWidth}/>
            </ScrollbarRoot>
        </SectionRoot>
    )
}

type TeammateProps = {
    profilePic: any
    name: string
    position: string
    sns: {
        linkedin?: string
        twitter?: string
        github?: string
        facebook?: string
        email?: string
        blog?: string
        rocketPunch?: string
        mastodon?: string
    }
}

const Teammate: React.FC<TeammateProps> = (props) => {
    return (
        <TeammateRoot>
            <TeammateCard>
                <TeammateProfilePic src={props.profilePic.default} alt={""}/>
                <TeammateName>{props.name}</TeammateName>
                <TeammatePosition>{props.position}</TeammatePosition>
                <TeammateComment>{props.children}</TeammateComment>
                <TeammateSnsRoot>
                    {props.sns.linkedin ? <TeammateSns href={props.sns.linkedin}>
                        <Linkedin src={icon_linkedin_off} alt={""}/>
                        <Linkedin src={icon_linkedin_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.github ? <TeammateSns href={props.sns.github}>
                        <Github src={icon_github_off} alt={""}/>
                        <Github src={icon_github_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.twitter ? <TeammateSns href={props.sns.twitter}>
                        <Twitter src={icon_twitter_off} alt={""}/>
                        <Twitter src={icon_twitter_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.mastodon ? <TeammateSns href={props.sns.mastodon}>
                        <Mastodon src={icon_mastodon_off} alt={""}/>
                        <Mastodon src={icon_mastodon_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.facebook ? <TeammateSns href={props.sns.facebook}>
                        <Facebook src={icon_facebook_off} alt={""}/>
                        <Facebook src={icon_facebook_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.blog ? <TeammateSns href={props.sns.blog}>
                        <Blog src={icon_blog_off} alt={""}/>
                        <Blog src={icon_blog_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.rocketPunch ? <TeammateSns href={props.sns.rocketPunch}>
                        <RocketPunch src={icon_rocketpunch_off} alt={""}/>
                        <RocketPunch src={icon_rocketpunch_on} alt={""}/>
                    </TeammateSns> : <></>}
                    {props.sns.email ? <TeammateSns href={props.sns.email}>
                        <Email src={icon_email_off} alt={""}/>
                        <Email src={icon_email_on} alt={""}/>
                    </TeammateSns> : <></>}
                </TeammateSnsRoot>
            </TeammateCard>
        </TeammateRoot>
    )
}

const EmptyTeammate: React.FC = () => {
    return (
        <EmptyTeammateRoot>
            <EmptyTeammateCard>
                <EmptyTeammateImageWrapper>
                    <img src={logo_enabled} alt={""}/>
                    <img src={logo_disabled} alt={""}/>
                </EmptyTeammateImageWrapper>
            </EmptyTeammateCard>
        </EmptyTeammateRoot>
    )
}

export default Teammates;
