import React, {useRef} from "react";
import styled from "styled-components";
import Typewriter, {TypewriterClass} from "typewriter-effect";

type Props = {
    onInit: (typewriter: TypewriterClass) => void
    currentScroll: number
}

const TypewriterWithPositionChecker: React.FC<Props> = (props) => {

    const positionChecker = useRef<HTMLDivElement>(null);

    return (
        <>
            <PositionRef ref={positionChecker}/>
            { positionChecker.current && props.currentScroll > positionChecker.current.getBoundingClientRect().top + window.scrollY - window.innerHeight - 10 ?
            <Typewriter
                onInit={props.onInit}/> : <></>
            }
        </>
    )
}

const PositionRef = styled.div`
  width: 0;
  height: 0;

  display: inline;
`

export default TypewriterWithPositionChecker;