import React, {useEffect, useRef, useState} from "react";

import {
    SlotMachineRoot, SlotMachineRail, SlotWrapper, Slot
} from "../../../styled_containers/areas/front/SlotMachineContainers";

type SlotMachineProps = {
    size: number
}

const SlotMachine: React.FC<SlotMachineProps> = (props) => {

    const [slotImages, setSlotImages] = useState<any[]>([]);

    const element = useRef<HTMLDivElement>(null);
    const position = useRef(0);
    const stack = useRef(0);
    const interpolateX = useRef(0);

    useEffect(() => {
        Promise.all(Array(props.size).fill(0).map((_, index) => import(`../../../assets/slotmachine/slot_${index}.png`)))
            .then((results) => {
                const real = shuffle(results);
                setSlotImages(real);
            });
    }, [props.size]);

    useEffect(() => {
        if (slotImages.length === 0) return;

        const interval = setInterval(() => {
            if (element.current == null) return;

            const maxTransformSize = 100 * (props.size / (props.size + 1));

            if (stack.current < 3) {
                if (position.current * (maxTransformSize / 30) < maxTransformSize)
                    position.current += 1;
                else {
                    position.current = 0;
                    stack.current++;
                }
                element.current.style.transform = `translateY(calc(9px - ${position.current * (maxTransformSize / 30)}%))`
            } else if (interpolateX.current < 200) {
                interpolateX.current += 1;
                element.current.style.transform = `translateY(calc(9px - ${interpolate(interpolateX.current * 0.005) * maxTransformSize}%))`;
            }
        }, 20);
        return () => clearInterval(interval);
    }, [slotImages, position, props.size]);

    return (
        <SlotMachineRoot>
            <SlotMachineRail ref={element} ready={slotImages.length !== 0}>
                {slotImages.map((slot, index) =>
                    <SlotWrapper key={`slot_${index}`}>
                        <Slot src={slot.default} alt={""}/>
                    </SlotWrapper>
                )}
                {slotImages[0] ?
                    <SlotWrapper><Slot src={slotImages[0].default} alt={""}/></SlotWrapper> :
                    <></>
                }
            </SlotMachineRail>
        </SlotMachineRoot>
    );
}

export default SlotMachine;

const interpolate = (x: number) => {
    return x === 1 ? 1 : 1 - Math.pow(2, -10 * x);
}

const shuffle = <T extends any>(array: T[]): T[] => {
    let currentIndex = array.length, randomIndex: number;

    while (currentIndex !== 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
    }

    return array;
}